import axios from "axios";
import qs from "qs";
    let request =  (url,data={},method="post") => {
        return new Promise((resolve,reject) => {

            data = qs.stringify(data,{encoder:function (str, defaultEncoder, charset, type){
                    if (type === 'value' || type === 'key') return encodeURIComponent(str);;
            }})
            // console.log(data);
            axios.request({
                // url:'/watish/api/auth/login?email=1057143786@qq.com&pwd=123456',
                // url:`/watish/api/${url}`,
                // url:`/api/${url}`,
                url:`./api/${url}`,
                data,
                method,
                timeout:2000
            }).then(value => {
                resolve(value.data)
            },reason => {
                reject(reason,{status:false});
            })
        })
    }
    let synchronizedRequest = requestArray => {
        let synchronizedResult = requestArray.map(async ele =>{
            return await request(ele.url,ele.data,ele.method)
        })
        return new Promise((resolve, reject) => {
            Promise.all(synchronizedResult).then(value => {
                resolve(value)
            },reason => {
                reject({status:false})
            })
        })

    }
export {request,synchronizedRequest}