import React, {Component} from 'react';
import background from './bg.jpg'
import {request} from "../../../Utils/request";
import PubSub from "pubsub-js";
import "./login.css"
import {message, notification} from "antd";
import {SmileOutlined} from "@ant-design/icons";
class Login extends Component {
    state = {
        active: 'login',
        loginEmail:"",
        loginPassword:"",
        registerEmail:'',
        registerPassword:"",
    }
    handleForm = () =>{
        return async () => {
            let{active,loginEmail,loginPassword,registerEmail,registerPassword} = this.state;
            switch (active){
                case "login":
                    try {
                        message.loading({ content: '登陆中...', key:'loading' });
                        let result = await request("auth/login",{email:loginEmail,pwd:loginPassword},"post")
                        console.log(result);
                        if (result.Ok){
                            PubSub.publish("toBreadNote",{token:result.Token})
                            message.success({ content: '登录成功!', key:'loading', duration: 2 });
                        }else {
                            message.warn({ content: '密码错误或用户名不存在', key:'loading', duration: 2 })
                        }
                    }catch (e) {
                        console.log(e);
                        message.warn({ content: '登陆失败', key:'loading', duration: 2 })
                    }

                    break;
                case "register" :
                    notification.info({
                        message:'提示',
                        description:'目前该项目处于测试状态,暂不支持账号的注册',
                        icon:<SmileOutlined />
                    })
                    break;
            }
        }

    }
    componentWillUnmount() {
        this.setState({},()=>{
            return false
        });
    }

    render() {
        let{active,loginEmail,loginPassword,registerEmail,registerPassword} = this.state;

        return (
            <div className="Login_Container">
                <div className="container">
                    <img src={background}/>
                    <div className="panel">
                        <div className="content login">
                            <div className="switch">
                                <span onClick={() =>{this.setState({active:"login"})}} className={`${active === "login" ? "active" : ""}`}>登陆</span>
                                <span>/</span>
                                <span onClick={() =>{this.setState({active:"register"})}} className={`${active === "register" ? "active" : ""}`}>注册</span>
                            </div>
                            <div className='form'>
                                {
                                    active === "register" ?
                                        <div>
                                            <div className="input">
                                                <input type="text" className={`${registerEmail || loginEmail? "hasValue" :""}`} ref={c =>this.registerEmail = c} onChange={() => this.setState({registerEmail:this.registerEmail.value})}/>
                                                <label>邮箱</label>
                                            </div>
                                            <div className="input">
                                                <input type="password" className={`${registerPassword || loginPassword ? "hasValue" :""}`} ref={c => this.registerPassword = c} onChange={() => this.setState({registerPassword:this.registerPassword.value})}/>
                                                <label>密码</label>
                                            </div>
                                        </div> :
                                        <div>
                                            <div className="input">
                                                <input type="text" className={`${loginEmail || registerEmail? "hasValue" :""}`}  ref={c => this.loginEmail = c} onChange={() => this.setState({loginEmail:this.loginEmail.value})}/>
                                                <label>邮箱</label>
                                            </div>
                                            <div className="input">
                                                <input type="password" className={`${loginPassword || registerPassword? "hasValue" :""}`} ref={c => this.loginPassword = c} onChange={() => this.setState({loginPassword:this.loginPassword.value})}/>
                                                <label >密码</label>
                                            </div>
                                        </div>
                                }
                                <button type="button" onClick={this.handleForm()}>{active === "login" ? "登录" : "注册"}</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        );
    }
}

export default Login;