//reducer是一个纯函数,接受两个参数preState 和 action
export default function TagListReducer(preState,action){
    //当preState为undefined则对其进行初始化操作
    if (preState === undefined || (preState.length === 1 && preState[0] === '')) preState = [];
    //从action中获取type和data type指明操作的动作data则是操作的数据
    const {type,TagListOrSingleTag} = action;
    console.log("TagListOrSingleTag",TagListOrSingleTag);
    /*  console.log("前一次的状态",preState);
      console.log("TagListOrSingleTag",TagListOrSingleTag);*/
    //根据type决定
    switch (type){
        case 'delete':
            return preState.filter(ele => ele !== TagListOrSingleTag);
        case 'add':
            console.log("add",[...preState, ...TagListOrSingleTag]);
            return [...preState,...TagListOrSingleTag] ;
        case "clear":
            return [];
        default:
            return preState;
    }
}